import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const ListLink = props => (
  <li style={{ display: `inline-block`, marginBottom: 0, marginRight: `1rem` }}>
    <Link
      style={{
        color: `white`,
      }}
      to={props.to}
    >
      {props.children}
    </Link>
  </li>
)

const Header = ({ siteTitle, description, social }) => (
  <header
    style={{
      background: `rebeccapurple`,
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >
          {siteTitle}
        </Link>
      </h1>
      <div
        style={{
          color: `white`,
          textDecoration: `none`,
          paddingTop: `1rem`,
        }}
      >
        <span>{description}</span>
        <ul
          style={{
            listStyle: `none`,
            display: `inline`,
            margin: 0,
            padding: 0,
            float: `right`,
          }}
        >
          {social.map((item, index) => (
            <li
              key={index}
              style={{
                display: `inline`,
                margin: 0,
                paddingLeft: `0.5rem`,
              }}
            >
              <a
                href={item.url}
                style={{
                  color: `white`,
                  textDecoration: `none`,
                }}
              >
                {item.name}
              </a>
            </li>
          ))}
        </ul>
      </div>
    </div>
    <div
      style={{
        width: `100%`,
        backgroundColor: `#ffcc06`,
        padding: `0.5rem`,
        textAlign: `center`,
      }}
    >
      <ul style={{ listStyle: `none`, margin: 0 }}>
        <ListLink to="/about/">About</ListLink>
        <ListLink to="/posts/">Posts</ListLink>
        <ListLink to="/tags/">Tags</ListLink>
      </ul>
    </div>
    <div
      style={{
        width: `100%`,
        backgroundColor: `#f2f2f2`,
        padding: `0.5rem`,
        textAlign: `center`,
      }}
    >
      <h4
        style={{
          margin: `0`,
        }}
      >
        <span role="img" aria-label="construction">
          🚧
        </span>
        사이트 공사 중
      </h4>
      <p
        style={{
          fontSize: `0.8rem`,
          margin: `0`,
        }}
      >
        일부 기능이 제대로 동작하지 않을 수 있습니다.
      </p>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
  description: PropTypes.string,
  social: PropTypes.array,
}

Header.defaultProps = {
  siteTitle: ``,
  description: ``,
  social: [],
}

export default Header
